.home {
    position: absolute;
    height: 100vh;
    width: 100%;
    z-index: 100;
    top: 0;

    background-color: #000;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}

.logo-box {
    position: relative;
    margin: 0;
    user-select: none;
    margin-bottom: 50px;
}

.home .title {
    color: #fff;
    font-size: 180px;
    font-family: EB Garamond,serif;
}

.home .logo-block {
    top: 10%;
    height: 80%;
    width: 180px;
    background-color: #bf642f;
    border-radius: 3px;
    position: absolute;
    z-index: -1;
}

.explore-btn {
    color: #fff;
    font-size: 20px;
}

.home .home-loading {
    color: #fff;
    font-size: 48px;
}